import React, { FC, forwardRef, memo } from 'react';
import cn from 'classnames';

import { RadioButtonProps } from './RadioButton.types';

export const RadioButton: FC<RadioButtonProps> = memo(
  forwardRef<HTMLDivElement, RadioButtonProps>(
    ({ className, disabled, checked, defaultChecked, hasError = false, onChange, children }: RadioButtonProps, ref) => {
      const radioCnString = cn(['thuiRadioButton', hasError && 'thuiRadioButton--error', className]);

      return (
        <div className={radioCnString} ref={ref}>
          <div className="thuiRadioButton__wrapper">
            <input
              type="radio"
              className="thuiRadioButton__input"
              disabled={disabled}
              checked={checked}
              defaultChecked={defaultChecked}
              onClick={onChange}
            />
            {children && (
              <span className={cn('thuiRadioButton__text', { 'thuiRadioButton__text--disabled': disabled })}>
                {children}
              </span>
            )}
          </div>
        </div>
      );
    }
  )
);
